@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda:wght@400;700&family=Poppins:wght@400;700&display=swap);
* {
    margin: 0;
    padding: 0;
}

body {
    background-color: #1a1a1a;
    color: white;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
}

section {
    padding: 20px;
}

h1,
h2,
h3,
h4 {
    font-family: 'Poppins', sans-serif;
}

#about {
    padding: 20px 30px;
    overflow: auto;
    max-width: 640px;
    margin: 0 auto;
}

#about h2 {
    margin-bottom: 20px;
}

#about p {
    text-align: left;
    text-indent: 20px;
    margin-bottom: 20px;
    max-width: 400px;
}

#about img {
    max-width: 40vw;
    margin-left: 5px;
    float: right;
    max-height: 30vh;
}

#about a {
    color: #b2bfa8;
}

#contact ul {
    list-style: none;
}

#contact ul li {
    margin-top: 10px;
}

#contact ul li a {
    text-decoration: none;
    color: #b2bfa8;
}

#contact ul li a:hover {
    color: #858f7e;
    text-decoration: underline;
}

header {
    background-color: #41473c;
    height: 10vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fefefe;
    box-shadow: 0 0 25px #00000040;
    width: 100vw;
}

header h1 {
    letter-spacing: 1px;
    text-align: left;
    margin-left: 5vw;
}

header a {
    margin-right: 5vw;
}

header img {
    width: 50px;
    -webkit-filter: invert();
            filter: invert();
}

header img:hover {
    -webkit-filter: none;
            filter: none;
}

.project-card {
    background-color: #b2bfa8;
    color: black;
    text-align: center;
    display: flex;
    /* width of card is 100vw - 20px (padding) */
    /* width of screenshot is half that */
    /* height of screenshot is width * 1.74 */
    max-height: calc(((100vw - 20px) / 2) * 1.74);
}

.screenshot img {
    width: calc((100vw - 20px) / 2);
    align-self: start;
}

.project-info {
    text-align: left;
    padding: 10px 0;
    position: relative;
}

.project-info h3 {
    text-align: center;
    font-size: 16px;
}

.project-links {
    display: flex;
    justify-content: space-around;
    margin-bottom: 5px;
}

.project-links a {
    color: #911500;
    font-size: 14px;
}

.project-links a:hover {
    color: #be3e28;
    font-weight: bold;
}

.project-desc {
    padding: 0 10px;
    font-size: 12px;
}

.learning-goals-and-tech {
    background-color: #71786c;
    color: white;
    position: absolute;
    width: 100%;
    bottom: 0;
}

.learning-goals-and-tech h4 {
    position: absolute;
    top: -10px;
    left: 10px;
    color: black;
    font-size: 14px;
}

.learning-goals {
    list-style: none;
    text-align: right;
    padding: 10px 10px 0 10px;
    font-size: 11px;
}

.learning-goals li {
    margin-bottom: 8px;
}

.project-tech {
    list-style: none;
    display: flex;
}

.project-tech li {
    margin-left: 5px;
}

@media (min-width: 500px) {
    .project-card {
        max-width: 400px;
        flex-direction: column;
    }

    .screenshot img {
        width: 400px;
    }

    .project-info h3 {
        font-size: 18px;
    }

    .project-links a {
        font-size: 16px;
    }

    .project-desc {
        font-size: 14px;
        height: 200px;
    }

    .learning-goals-and-tech h4 {
        font-size: 16px;
    }

    .learning-goals {
        font-size: 12px;
    }
}

@media (max-width: 500px) {
    .project-card .tech-icon {
        width: 30px;
    }
}

@media (max-width: 350px) {
    .project-desc {
        font-size: 10px;
    }

    .learning-goals-and-tech h4 {
        font-size: 12px;
    }

    .learning-goals {
        font-size: 9px;
    }
}

@media (max-width: 310px) {
    .project-info h3 {
        font-size: 12px;
    }

    .project-links a {
        font-size: 10px;
    }

    .project-desc {
        font-size: 9px;
    }

    .learning-goals-and-tech h4 {
        font-size: 10px;
    }

    .learning-goals {
        font-size: 7px;
    }
}

.tech-icon {
    width: 35px;
}

#projects {
    padding: 20px 10px;
}

#projects h2 {
    margin-bottom: 20px;
}

#projects-container {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    justify-content: center;
}

@media (min-width: 500px) {
    #projects-container {
        flex-direction: row;
        flex-wrap: wrap;
    }
}

#skills h2 {
    margin-bottom: 20px;
}

#skills ul {
    list-style: none;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.skill-item {
    display: flex;
    flex-direction: column;
    align-items: center;
}


